.radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.radio + label {
  position: relative;
  cursor: pointer;
}
.radio + label:before {
  content: '';
  position: absolute;
  border-radius: 50%;
  background: #FFF;
}

.Panel--ios .radio + label:before {
  width: 20px;
  height: 20px;
  border: 2px solid #AEBFCF;
}

.Panel--android .radio + label:before {
  width: 14px;
  height: 14px;
  border: 2px solid #AEBFCF;
}

.radio + label:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  opacity: 0;
  -webkit-transition: .2s;
  transition: .2s;
}

.Panel--ios .radio + label:after {
  top: 5px;
  left: 5px;
  width: 14px;
  height: 14px;
  background: #528BCC;
}

.Panel--android .radio + label:after {
  top: 5px;
  left: 5px;
  width: 8px;
  height: 8px;
  background: #4986CC;
}



.radio:checked + label:after {
  opacity: 1;
}

.Panel--ios .radio:checked + label:before {
  border-color: #528BCC;
}

.Panel--android .radio:checked + label:before {
  border-color: #4986CC;
}


